  font-size: unset;
  text-align: unset;
}
a {
  text-decoration: none;
  cursor: url(data:image/png;base64,),
    pointer;
}
.adaptive-text-x {
  font-size: clamp(1rem, 5dvw, 2rem);
}
.adaptive-text-m {
  font-size: clamp(0.6rem, 4dvw, 1.5rem);
}
.font-pixel * {
